// exporter la fonction pour la retrouver dans le reducer
export const SET_STABLE_STATE = "SET_STABLE_STATE";

// elle enverra le boolen au store en temps rel, l'action rcupre ce que l'utilisateur  ralis et indique qu'il faut changer l'tat du boolen
export const setStableState = (bool) => {
  return (dispatch) => {
    return dispatch({ type: SET_STABLE_STATE, payload: bool });
  };
};
